<template>
  <div>
    <div class="contain">
      <div class="title-area">
        <div class="title"></div>
        <div class="s-title"></div>
      </div>

      <div class="info-area">
        <div v-for="infos in informations" :key="infos.id" class="info-item">
          <div class="item-title">{{ infos.title }}</div>
          <div class="item-contain">{{ infos.contain }}</div>
        </div>
      </div>
    </div>
    <note-foot class="bg-gray"></note-foot>
  </div>
</template>
<script>
import NoteFoot from "@/components/NoteFoot.vue";

export default {
  data() {
    return {
      informations: [
        {
          id: 0,
          title: "",
          contain:
            "2019, , : , : , BAT, "
        },
        {
          id: 1,
          title: "",
          contain:
            "2019, , : , : , BAT, "
        },
        {
          id: 2,
          title: "",
          contain:
            "2019, , : , : , BAT, "
        }
      ]
    };
  },
  components: {
    "note-foot": NoteFoot
  }
};
</script>
<style scoped>
.bg-gray {
  background-color: #dcdcdc;
}
.contain {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 98vw;
  height: 94.63vh;
  margin: 0 auto;
  /* box-shadow: 0px 0px 0px 1px #f7f7f7; */
  border-top: 1px solid #f7f7f7;
}
.contain .title-area {
  width: 78.44vw;
  height: 15vh;
  padding-left: 3.24vh;
  border-bottom: 1px solid #eeeeee;
}
.contain .title-area .title {
  margin-top: 5.84vh;
  font-size: 18px;
  font-family: SimHei;
  font-weight: 400;
  color: #000000;
}
.contain .title-area .s-title {
  margin-top: 24px;
  font-size: 12px;
  font-family: SimHei;
  font-weight: 400;
  color: #878787;
}

.contain .info-area {
  display: flex;
  flex-direction: column;
  width: 78.44vw;
  height: 79.63vh;
}
.contain .info-item {
  flex: 1;
}
.contain .info-item .item-title {
  margin-top: 3.70vh;
  font-size: 18px;
  font-family: SimHei;
  font-weight: 400;
  color: #010101;
  line-height: 68px;
}
.contain .info-item .item-contain {
  font-size: 18px;
  font-family: SimHei;
  font-weight: 400;
  color: #333333;
  line-height: 36px;
}
</style>



