<template>
  <div>
    <div class="product-detail">
      <div class="p-line-left"></div>
      <div class="p-contain">
        <div class="p-contain-info">{{$t('sale.afterSale')}}</div>
      </div>
      <div class="p-info"></div>
      <div class="p-line-right"></div>
    </div>
    <div class="sale-info-area">
      <div class="btn-area">
        <button :class="activeBtn === 'question' ? 'btn nmt' : 'btn'" @click="btnEvent('question')"></button>
        <button :class="activeBtn === 'service' ? 'btn nmt' : 'btn'" @click="btnEvent('service')"></button>
        <button :class="activeBtn === 'policy' ? 'btn nmt' : 'btn'" @click="btnEvent('policy')"></button>
      </div>
      <div class="m-sale-title">{{ mtitle }}</div>
      <div class="m-notice"></div>
      <!--  -->
      <div class="sale-info">
        <div v-for="infos in information" :key="infos.id" :style=" infos.title ? 'margin-top: 14px;' : '' ">
          <div v-if="infos.title" style="line-height: 20px;">{{ infos.title }}</div>
          <div style="line-height: 20px;">{{ infos.contain }}</div>
        </div>
      </div>
    </div>
    <note-foot class="bg-gray"></note-foot>
  </div>
</template>

<script>
import NoteFoot from "@/components/NoteFoot.vue";
export default {
  components: {
    "note-foot": NoteFoot
  },
  data() {
    return {
      information: [],
      activeBtn: 'question',
      mtitle: '',
      question: [
        {
          id: 0,
          title: "1B1?",
          contain:
            " 813819B12(5. 7)"
        },
        {
          id: 1,
          title: "2i2?",
          contain:
            " App Store ()(APP, APP "
        },
        {
          id: 2,
          title: "3?",
          contain:
            ""
        },
        {
          id: 3,
          title: "4?",
          contain:
            "Micro USB 5VUSB"
        },
        {
          id: 4,
          title: "5?",
          contain:
            "2.5"
        },
        {
          id: 5,
          title: "6",
          contain:
            ":"
        },
        {
          id: 6,
          title: "7?",
          contain: ""
        },
        {
          id: 7,
          title: "8?",
          contain:
            "70'C8"
        },
        {
          id: 8,
          title: "9?",
          contain:
            "1 ~20"
        }
      ],
      service: [
        {
          id: 0,
          title: "",
          contain:
            " //():"
        },
        {
          id: 1,
          title: "",
          contain:
            "1.15;"
        },
        {
          id: 2,
          title: "",
          contain:
            "2.12"
        },
        {
          id: 3,
          title: "",
          contain:
            "1.///////"
        },
        {
          id: 4,
          title: "",
          contain:
            "2."
        },
        {
          id: 5,
          title: "",
          contain:
            "3."
        }
      ],
      policy: [
        {
          id: 0,
          title: "",
          contain:
            "// ()"
        }
      ]
    }
  },
  watch: {
    $route(to, from) {
      const e = this.$route.query.type
      if(e === 'question'){
        this.information = this.question
        this.mtitle = ''
      }
      if(e === 'service'){
        this.information = this.service
        this.mtitle = ''
      }
      if(e === 'policy'){
        this.information = this.policy
        this.mtitle = ''
      }
    }
  },
  mounted() {
    this.information = this.question
  },
  methods: {
    btnEvent(e) {
      this.activeBtn = e
      if(e === 'question'){
        this.information = this.question
      }
      if(e === 'service'){
        this.information = this.service
      }
      if(e === 'policy'){
        this.information = this.policy
      }
    }
  }
};
</script>
<style scoped>
@media screen and (max-width: 1200px) {
  .product-detail {
    display: none;
  }
  .sale-info-area .btn-area {
    display: none;
  }

  .sale-info-area {
    background-color: #f6f6f6;
  }
  .sale-info-area .m-sale-title {
    height: 64px;
    padding-top: 40px;
    font-size: 34px;
    font-family: SimHei;
    font-weight: bold;
    color: #333333;
    text-align: center;
    line-height: 64px;
  }
  .sale-info-area .m-notice {
    margin: 30px 40px 0 40px;
    padding-bottom: 14px;
    font-size: 26px;
    color: #ccc;
    border-bottom: 1px solid #333;
  }
  .sale-info-area .sale-info {
    min-height: 100vh;
    padding: 40px;
    font-size: 28px;
    font-family: SimHei;
    font-weight: 400;
    color: #666666;
    line-height: 56px;
  }
}

@media screen and (min-width: 1199px) {
  .bg-gray {
    background-color: #dcdcdc;
  }
  .sale-info-area .m-sale-title {
    display: none;
  }
  .sale-info-area .m-notice { 
    display: none;
  }
  button {
    margin: 0;
    padding: 0;
    border: 1px solid transparent;
    background-color: transparent;
    outline: none;
  }
  .product-detail {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    height: 162px;
    font-size: 28px;
    font-family: SimHei;
    color: #222222;
  }

  .product-detail .p-contain {
    flex: 2;
    display: flex;
    align-items: flex-end;
  }

  .product-detail .p-contain-info {
    margin: 0 auto;
    margin-bottom: 10px;
  }

  .product-detail .p-line-left {
    position: absolute;
    top: 80px;
    left: 40%;
    width: 112px;
    height: 1px;
    background-color: #d2d2d2;
  }
  .product-detail .p-line-right {
    position: absolute;
    top: 80px;
    right: 40%;
    width: 112px;
    height: 1px;
    background-color: #d2d2d2;
  }
  .product-detail .p-info {
    flex: 1;
    font-size: 16px;
    color: #777777;
    font-family: MyriadPro-Regular;
    font-weight: 400;
    text-align: center;
    /* line-height: 20px; */
  }
  .sale-info-area {
    display: flex;
    min-height: 800px;
    margin-top: 20px;
    margin-bottom: 100px;
  }
  .sale-info-area .btn-area {
    width: 24.69vw;
    height: 75.68vh;
  }

  .sale-info-area .btn-area .btn {
    width: 164px;
    height: 42px;
    cursor: pointer;
    margin-top: 60px;
    margin-left: 12.19vw;
    color: #515151;
    border: 1px solid #a0a0a0;
    border-radius: 4px;
  }
  .sale-info-area .btn-area .btn.nmt {
    color: #fff;
    background-color: #515151;
  }  
  .sale-info-area .btn-area .btn:hover {
    color: #ffffff;
    background-color: #515151;
  }
  .sale-info-area .sale-info {
    width: 63.96vw;
    height: 72.04vh;
    font-size: 18px;
    font-family: SimHei;
    font-weight: 400;
    color: #000000;
    line-height: 40px;
  }
}
</style>


